import { Component } from '@angular/core';
import { ViewController,IonicPage,AlertController ,NavParams , NavController} from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { Storage } from '@ionic/storage';
import { FormGroup} from '@angular/forms';
import { ControlBase } from '../../../common/forms/control-base';
import { ControlsService } from '../../../common/forms/controls.service';
import { TranslateService } from '@ngx-translate/core';

//import 'rxjs/add/operator/map';

@IonicPage()
@Component({
  selector: 'page-formulario-movil',
  templateUrl: 'formulario-movil.html',
})
export class FormularioMovilPage {
  controls: ControlBase<any>[];
	form: FormGroup;
	items2: any=[];
	submitted: any=[];
	username: string = "";
	readonly FILENAME = '../../assets/data/data1.json';
	idcat:any;
	idempresa:any;
	idcatn:any;
	idfrm:any;
	idfrmn:any;
	public tt:any;
	muestrabien:boolean=false

	constructor(private translateService: TranslateService,public storage: Storage, public msj: NotificationProvider
		,public navCtrl: NavController,   public regService: Auth,		public controlsService: ControlsService,
							public alertCtrl: AlertController,public viewCtrl: ViewController, public navParams: NavParams
						) {
		this.form = new FormGroup({});


    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value


  })

	}




  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
	});


	}
	ionViewDidLoad() {


    this.idcat=this.navParams.data.record2.idcat;
    this.idcatn=this.navParams.data.record2.idcatn;
		this.idfrm= this.navParams.data.record2.idfrm;
		this.idfrmn=this.navParams.data.record2.idfrmn;
	}

  public davaloritem(data,campo){
		var val=''
		if(data[campo])
		{val=data[campo]}
		else
		{val=''}
		return val;
	}




	public dalistacat(data,op){
		var val:any;
		for(var i = 0; i <data.length; i++) {
				 if(data[i].label==op)
				 {

					val={key:data[i].key,label:data[i].label};
					break;
				 }
		}

		return val;
	}


	public daopciones(data,id){
		var val:any[]
		for(var i = 0; i <data.length; i++) {
				 if(data[i].idunidad==id)
				 {

					val.push({key:data[i]._id,label:data[i].nombre});
				 }
		}

		return val;
	}

	ionViewWillEnter() {

		if(this.navParams.get("record")==null)
		{//nuevo


			this.regService.getReg(this.idfrm+ '/formulariod/' + this.idempresa	, 'frmmovils').then((data) => {
				var aa:any=[]
				var len:any = data;
				for(var i = 0; i <len.length; i++) {
				var rquer=len[i].required;var sireque=''
				var valort=''
				var valor2t:any=[];

				switch (len[i].type) {
					case 'label':   valort='.';  break;
					case 'check': 	valort=len[i].default;
					 if(valort==''){valort='false';}			 else				 {		if(!valort){valort='false'};		 }  break;
					case 'select':   valor2t=this.dalistacat(len[i].options,len[i].default);  break;
					default:
					valort=len[i].default
				}

				if(!valor2t){valor2t=''}
				if(!valort){valort=''}
				if(rquer=='true'){sireque=" (*)"}


						if(len[i].type=='select'){
							aa.push({
								"order":len[i].order,	"name": len[i].name,"type":len[i].type,"required": len[i].required,
								"placeholder":len[i].placeholder,	"display": len[i].display,"selected": len[i].selected,//true
								"disabled": len[i].disabled,	"hidden": len[i].hidden,	"title": len[i].title + sireque,
								"position":len[i].position,	"labelsizefondt":len[i].labelsizefondt,"default":len[i].default,"valor":valor2t,
								"categoria":len[i].categoria,"combofijo":len[i].combofijo,"idcadmovil":this.tt ,
								"options": len[i].options,	"fondoetiqueta": len[i].fondoetiqueta,
									"coloretiqueta": len[i].coloretiqueta,	"blike": len[i].blike,"rangomin": len[i].rangomin,	"rangomax": len[i].rangomax,	"rangostep": len[i].rangostep,	"alfatypo": len[i].alfatypo	})



						}
						else
						{
							aa.push({
								"order":len[i].order,	"name": len[i].name,"type":len[i].type,"required": len[i].required,
								"placeholder":len[i].placeholder,	"display": len[i].display,"selected": len[i].selected,//true
								"disabled": len[i].disabled,	"hidden": len[i].hidden,	"title": len[i].title + sireque,
								"position":len[i].position,	"labelsizefondt":len[i].labelsizefondt,"default":len[i].default,"valor":valort,
								"categoria":len[i].categoria,"combofijo":len[i].combofijo,"idcadmovil":this.tt ,
								"options": len[i].options,	"fondoetiqueta": len[i].fondoetiqueta,
								"coloretiqueta": len[i].coloretiqueta,	"blike": len[i].blike,"rangomin": len[i].rangomin,	"rangomax": len[i].rangomax,	"rangostep": len[i].rangostep,	"alfatypo": len[i].alfatypo	})

						}


				}
				this.controls = this.controlsService.getControls(aa);
				this.muestrabien=true;
	}, (err) => {
		this.msj.showError3('Error',err.error);

	});





		}
		else
		{//actualiza
			this.regService.getReg(this.idfrm+  '/formulariod/' +this.idempresa, 'frmmovils').then((data) => {
				var aa:any=[]
				var len:any = data;
				for(var i = 0; i <len.length; i++) {
					var rquer=len[i].required;var sireque=''
					if(rquer=='true'){sireque=" (*)"}

					var valort=this.davaloritem(this.navParams.data.record,len[i].name)

					if(!valort){valort=''}
					aa.push({
						"order":len[i].order,	"name": len[i].name,"type":len[i].type,"required": len[i].required,
						"placeholder":len[i].placeholder,	"display": len[i].display,"selected": len[i].selected,//true
						"disabled": len[i].disabled,	"hidden": len[i].hidden,	"title": len[i].title  + sireque,
						"position":len[i].position,	"labelsizefondt":len[i].labelsizefondt,"default":''
						,"valor":valort
						,"categoria":len[i].categoria,"combofijo":len[i].combofijo,"idcadmovil":this.tt ,
						"options": len[i].options,	"fondoetiqueta": len[i].fondoetiqueta,
						"coloretiqueta": len[i].coloretiqueta,	"blike": len[i].blike,"rangomin": len[i].rangomin,	"rangomax": len[i].rangomax,	"rangostep": len[i].rangostep,	"alfatypo": len[i].alfatypo	})
				}

				this.controls = this.controlsService.getControls(aa);
				this.muestrabien=true;
	}, (err) => {
		this.msj.showError3('Error',err.error);

	});

		}

this.form.valueChanges
.subscribe(val => {
	this.submitted = val;
});

	}
	submitForm($event){
	if(this.form.status=='INVALID')
	{
		this.msj.showError3('Error',"Ingrese todos los campos requeridos en el formulario");


	}
	else
	{
		let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

		if(this.navParams.get("record")==null)
		{
			this.submitted['usuarionew'] = this.username;
			this.submitted['usuarioup'] = this.username;
			this.submitted['idempresa'] = this.idempresa;
			let   options       : any	     = {
				idcat: this.idcat,
				idform:this.idfrm,
				estructura:this.submitted,
				bitacora:{email: this.username,permiso:creat,accion:creat+' formulario movil'}
			 };



			if(options){
				this.regService.createRegmovil('',options,'frmmovils').then((result) => {
					this.viewCtrl.dismiss(result);

			}, (err) => {
				this.msj.showError3('Error',err.error);
			});
			}

		}
		else{

		//this.submitted['usuarionew'] = this.navParams.data.record.usuarionew;
			this.submitted['usuarioup'] = this.username;
			this.submitted['idempresa'] = this.idempresa;

			let   options       : any	     = {
				idcat: this.idcat,
				idform:this.idfrm,
				estructura:this.submitted,
				bitacora:{email: this.username,permiso:creat,accion:creat+' formulario movil'}
			 };

			if(options){
				this.regService.createRegmovil(this.navParams.data.record._id,options,'frmmovils').then((result) => {
					this.viewCtrl.dismiss(result);

			}, (err) => {
				this.msj.showError3('Error',err.error);
			});
			}



		}
	}


	}

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
  //  if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


}



