import { Injectable } from '@angular/core';
import { ControlDescriptor } from './control';
import { SelectControl } from './select.control';
import { TextareaControl } from './textarea.control';
import { TextboxControl } from './textbox.control';
import { LabelControl } from './label.control';
import { NoteControl } from './note.control';


import { RangoControl } from './rango.control';
import { FechaControl } from './Fecha.control';
import { CheckControl } from './check.control';
import { FechahoraControl } from './fechahora.control';
import { HoraControl } from './hora.control';
import { ImageControl } from './image.control';


@Injectable()
export class ControlsService {
  getControls(descriptors: ControlDescriptor[]) {
    let controls = descriptors.map((descriptor, index) => {
      let   options = {
        ...descriptor,
        type: descriptor.type,
        key: descriptor.name,
        label: descriptor.title,
        value: descriptor.valor,
        required: descriptor.required,
        order:  descriptor.order
      };

      switch (descriptor.type) {
            case 'text':
            case 'Number':
              return new TextboxControl(options);
            case 'textarea':
              return new TextareaControl(options);
            case 'select':
              return new SelectControl(options);
            case 'label':
                return new LabelControl(options);
            case 'note':
            return new NoteControl(options);
            case 'rango':
            return new RangoControl(options);
            case 'fecha':
            return new FechaControl(options);
            case 'fechahora':
            return new FechahoraControl(options);
            case 'hora':
            return new HoraControl(options);
            case 'check':
            return new CheckControl(options);
            case 'imagen':
            return new ImageControl(options);
            default:
          console.error(`${descriptor.type} is not supported`);
      }
    });

    return controls.filter(x => !!x).sort((a, b) => a.order - b.order);
  }
}


