import { Component, EventEmitter, Input, OnInit, Output , ViewChild } from '@angular/core';
import { ModalController,ActionSheetController} from 'ionic-angular';
import { FormGroup } from '@angular/forms';
import { ControlBase } from './control-base';
import { DynamicControlsService } from './dynamic-controls.service';
import { ImageProvider } from '../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { TranslateService } from '@ngx-translate/core';
//import { ImageModalPage } from '../../pages/WEBAPP/image-modal/image-modal';
   
@Component({
  selector: 'dynamic-form',
  templateUrl: './dynamic-form.component.html',   
  exportAs: 'dynamicForm'
})
export class DynamicFormComponent implements OnInit {
/*
<ion-slides [options]="sliderOpts">
    <ion-slide *ngFor="let img of [1, 2, 3]">
      <img src="assets/img/{{ img }}.png" tappable (click)="openPreview(img)">
    </ion-slide>
  </ion-slides>

  ion-slides {
    margin-top: 30%;
    background: #e6e6e6;
 
    img {
        padding-top: 20px;
        padding-bottom: 20px;
    }
}
*/

  sliderOpts = {
    zoom: false,
    slidesPerView: 1.5,
    spaceBetween: 20,
    centeredSlides: true
  };
 
  @ViewChild('fileInput') fileInput;
  //@ViewChild('myImage') myImage;
  // <img class="circle-pic"  height="150 px" width="150 px" #myImage  >  
  @Input() controls: ControlBase<any>[] = [];
  @Input() form: FormGroup;
  @Output() submit: EventEmitter<any> = new EventEmitter();
  public f1              	: any;
  imagentemp:any;
  constructor(public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController, private _IMAGE       : ImageProvider,private controlsService: DynamicControlsService, public translateService: TranslateService) {
 

  }

  //ngOnChanges() {}
  //ngAfterContentInit(){  }
  //ngDoCheck(){}
  //ngAfterViewChecked(){}
  //ngOnDestroy(){}

  //ngAfterContentChecked() { console.log("1");}
  ngAfterViewInit()
    {
      for(let i =0; i <this.controls.length; i++){
        var aa:any;
        aa=this.controls[i]
        if(aa.type=='imagen')
        {
          var BB= document.getElementById(  aa.key + "xx")
          if(BB) {   BB.setAttribute("src",aa.value);    }
        }
      }
  }
  pushPage(){
    console.log("Looks like I'm about to leave :(");
  }
  ionViewWillLeave() {
    console.log("Looks like I'm about to leave :(");
  }

  ngOnInit() {
    this.form = this.controlsService.toFormGroup(this.form, this.controls);

  
  }   

  openPreview(op2,op3) {
    var valor=this.form.value[op2]
    var modalPage2 = this.modalCtrl.create('ImageModalPage',{imgv:valor});
    modalPage2.present();
  }

  ionViewDidLoad() : void
 {
 //poner todas las imagenes




}

  onSubmit() {
    this.submit.next(this.form.value);
  }

  public poneimg(selectedValue: any,op2,op3) {


  }
  public showimg(op2,op3) {
    var valorXX=this.form.value[op2]

    var modalPage2 = this.modalCtrl.create('VerimagenPage',{img:valorXX});
    modalPage2.present();
  }
  onSelectBlur(selectedValue: any,op2,op3) {
  var arr=this.form.value;
  var valorseleccionado=arr[op2]
        switch (op2) {
          case 'fecha':
          arr[op2]=valorseleccionado+'T00:00:00Z'
          this.form.setValue(arr);
            
          break
          case 'hora':
          arr[op2]='2019-01-01T'+valorseleccionado+':00Z'
          this.form.setValue(arr);
          
          break
          default:
      }
        //patchValue(arr,{onlySelf : true,emitEvent : false }
  //2019-03-29T15:13:00Z

  }
  //this.maintenance.controls[“firstname”].setValue(this.firstname);
  updateProfileImage(selectedValue: any,op2,op3) {
    this.imagentemp=op2

    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph(selectedValue,  this.imagentemp,op3);
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage(selectedValue,  this.imagentemp,op3);
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph(selectedValue: any,op2,op3) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
//          this.f1 	= image.toString();
            this.f1 = image.toString()
            this._IMAGE.generateFromImage(this.f1, 200, 200, 0.5, data => {
              var arr=this.form.value;
              arr[op2]=data
              this.form.setValue(arr);
              this.f1=data
              var BB= document.getElementById(   this.imagentemp + "xx")
              if(BB) {   BB.setAttribute("src",data);    }
            
            });


     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  
  selectImage(selectedValue: any,op2,op3) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
      this.f1 = image.toString()
      this._IMAGE.generateFromImage(this.f1, 200, 200, 0.5, data => {
        var arr=this.form.value;
        arr[op2]=data
        this.form.setValue(arr);
        this.f1=data
        var BB= document.getElementById(   this.imagentemp + "xx")
        if(BB) {   BB.setAttribute("src",data);    }

      
      });



     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }   
  }
  
  public processWebImage(event: any,op2,op3): void {
      if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
            this.f1 = readerEvent.target.result;
            this._IMAGE.generateFromImage(this.f1, 200, 200, 0.5, data => {
              var arr=this.form.value;
              arr[  this.imagentemp]=data
              this.form.setValue(arr);   
              this.f1=data
              var BB= document.getElementById(   this.imagentemp + "xx")
              if(BB) {   BB.setAttribute("src",data);    }

           
            });
      };
      reader.readAsDataURL(event.target.files[0]);
    }
}

}


